@import url("https://fonts.googleapis.com/css2?family=Rift:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-outfit: Outfit;
  --font-rift: Rift;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-4xl-6: 23.6px;
  --font-size-lg: 18px;
  --font-size-11xl-5: 30.5px;
  --font-size-3xl: 22px;
  --font-size-30xl: 49px;
  --font-size-16xl-4: 35.4px;
  --font-size-mini-8: 14.8px;
  --font-size-3xl-1: 22.1px;
  --font-size-7xl-5: 26.5px;
  --font-size-sm-1: 13.1px;

  /* Colors */
  --color-white: #fff;
  --color-coral-100: #f36d45;
  --color-coral-200: #f36d40;
  --color-black: #000;
  --color-gray-100: #282a37;
  --color-dimgray: #575757;
  --gray-900: #2a3342;
  --color-mediumblue: #1d41ff;
  --color-darkgray: #a8a8a8;

  /* Gaps */
  --gap-57xl: 76px;
  --gap-7xs-3: 5.3px;

  /* Paddings */
  --padding-19xl: 38px;
  --padding-81xl: 100px;
  --padding-83xl: 102px;
  --padding-56xl: 75px;
  --padding-5xl: 24px;
  --padding-43xl: 62px;
  --padding-7xl-5: 26.5px;
  --padding-mid-7: 17.7px;
  --padding-lg-4: 18.4px;
  --padding-mid-5: 17.5px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-8xs: 5px;
  --br-8xs-7: 4.7px;
  --br-sm-5: 13.5px;
  --br-3xs: 10px;
  --br-9xs-5: 3.5px;
}
